.navigation-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 6.6rem 11.45% 0 5.5rem;
}
.navigation {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
  margin-top: 3px;
}
.navigation ul {
  display: flex;
}
.navigation ul span {
  font-weight: 700;
  margin-right: 1.2rem;
}
.navigation ul span,
.navigation ul li a {
  font-size: var(--nav-fs);
  color: var(--white);
  letter-spacing: 0.27rem;
  text-transform: uppercase;
}
.navigation ul li {
  padding: 3.33rem 0;
  border-bottom: 3px solid transparent;
  transition: border-bottom 0.2s ease-out;
}
.active {
  padding: 3.33rem 0;
  border-bottom: 3px solid var(--white) !important;
}
.navigation ul li:not(:last-child) {
  margin-right: 5rem;
}
.navigation ul li:hover {
  border-bottom: 3px solid var(--white-darker);
}
.line {
  display: block;
  height: 1px;
  width: 30.84%;
  left: 16.7rem;
  background: var(--white-darker);
  z-index: 10;
}

@media only screen and (max-width: 1200px) {
  .navigation-container {
    margin: 6.4rem 7.44% 0 5.5rem;
  }
  .line {
    display: none;
  }
}
@media only screen and (max-width: 1024px) {
  .navigation-container {
    margin: 6.4rem 8.44% 0 5.5rem;
  }
}
@media only screen and (max-width: 768px) {
  .navigation ul span {
    display: none;
  }
  .navigation ul li a {
    font-size: 1.4rem;
    letter-spacing: 0.236rem;
  }
  .navigation ul li {
    padding: 3.6rem 0;
  }
  .active {
    padding: 3.6rem 0;
  }
  .navigation ul li:not(:last-child) {
    margin-right: 3.7rem;
  }
  .navigation-container {
    margin: 0 4.6rem 0 3.9rem;
  }
}
@media only screen and (max-width: 520px) {
  .navigation ul span {
    display: none;
  }
  .navigation ul li a {
    font-size: 1.4rem;
    letter-spacing: 0.236rem;
  }
  .navigation ul li {
    padding: 3.6rem 0;
  }
  .active {
    padding: 3.6rem 0;
  }
  .navigation ul li:not(:last-child) {
    margin-right: 3.7rem;
  }
  .navigation-container {
    margin: 0;
  }
}
@media only screen and (max-width: 520px) {
  .navigation {
    width: 100%;
    justify-content: center;
    margin-top: 11.8rem;
    align-items: inherit;
  }
  .navigation ul {
    display: flex;
    flex-direction: column;
  }
  .navigation ul span {
    margin-right: 1.1rem;
    display: inherit;
  }
  .navigation ul span,
  .navigation ul li a {
    font-size: 1.6rem;
    letter-spacing: 0.192rem;
    text-transform: uppercase;
    letter-spacing: 2.7px;
  }
  .navigation ul li {
    padding: 1rem 0;
    border-bottom: 2px solid transparent;
    transition: border-bottom 0.2s ease-out;
  }
  .navigation ul li:not(:last-child) {
    margin-bottom: 3.2rem;
  }
  .navigation ul li:hover {
    border-bottom: 2px solid var(--white-darker);
  }
  .active {
    padding: 1rem 0;
    border-bottom: 2px solid var(--white) !important;
  }
}
